.Menu {
  list-style-type: none;
  margin: 1rem;
  padding: 0;
}

@media (min-width: 430px) and (max-width: 914px) {
  .Dish {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .Dish__footer {
    grid-column: 1 / 3;
  }
}

@media (min-width: 915px) {
  .Menu {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 1rem;
  }
}



