.Dish {
  border-bottom: 0.3rem solid var(--color-primary);
  border-radius: 0.4rem;
  border-top: 0.3rem solid var(--color-primary);
  -webkit-box-shadow: 0 0.1rem 0.2rem var(--color-dark-shadow);
          box-shadow: 0 0.1rem 0.2rem var(--color-dark-shadow);
  margin-bottom: 2rem;
  text-align: center;
}

.Dish__header {
  background-color: var(--color-white);
  padding: 0.5rem 0 1rem;
  font-size: 1.6rem;
}

.Dish__name {
  font-family: 'Roboto Slab', serif;
  font-weight: bold;
  font-size: 120%;
  line-height: 1.5;
  margin: 0;
  text-shadow: 0 0.1rem 0.1rem var(--color-dark-shadow);
}

.Dish__subtitle {
  color: var(--color-brown);
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 70%;
  font-weight: 300;
  margin: 0;
}

.Dish__body {
  background-color: var(--color-white);
  font-size: 1.3rem;
  padding: 0 1rem 1rem;
}

.Dish__footer {
  background-color: hsla(0, 0%, 100%, 0.9);
}

.Dish__link-wrapper {
  margin: 0;
}

.Dish__price-wrapper {
  background-color: hsla(0, 0%, 100%, 0.8);
  font-size: 1.5rem;
  margin: 0;
  padding: 0.3rem 0;
}

.Dish__link-buy {
  background: var(--color-primary);
  border-radius: 2rem;
  color: var(--color-secondary);
  display: inline-block;
  font-size: 1.5rem;
  font-weight: bold;
  font-family: 'Roboto Slab';
  margin-bottom: 0.5rem;
  padding: 0.25rem 1rem;
}



