.MenuWritten {
  padding: 0.5rem;
}

.MenuWritten__size-group {
  background-color: var(--color-white);
  border: 0.1rem solid var(--color-dark-gray);
  border-radius: 0.75rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-bottom: 1rem;
  padding: 0.5rem;
}

.MenuWritten__title {
  font-family: 'Roboto Slab', serif;
  font-size: 1.75rem;
  margin: 0;
  text-align: center;
}

.MenuWritten__subtitle {
  color: var(--color-brown);
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.2rem;
  font-weight: 100;
  margin-top: 0;
  text-align: center;
}

.MenuWritten__dishes--small {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Ingredient--primary {
  font-size: 130%;
  font-weight: bold;
}

.Ingredient::after {
  content: '+';
  display: inline-block;
  font-weight: bold;
  margin: 0 0.3rem;
}

.Ingredient:last-child::after {
  display: none;
}



