.Header {
  margin-bottom: 1.5rem;
  padding-top: 2rem;
}

.Header__logo-wrapper {
  text-align: center;
}

.Header__slogan {
  display: block;
  font-family: 'Roboto Condensed', serif;
  font-weight: bold;
  font-size: 1.8rem;
  font-style: italic;
  text-shadow: 0 0 0.2rem var(--color-light-shadow), 0 0 0.4rem var(--color-light-shadow);
}



